    <p>A tenso aplicada na armadura de protenso decresce em razo do tempo, isso se d atravs da diminuio do alongamento da armadura, o que acarreta na reduo da fora de protenso, que  gerada por diferentes causas, estas por sua vez so denominadas de perdas de protenso e ocorrem de forma imediata (perdas imediatas) ou ao longo do tempo (perdas progressivas). As perdas imediatas podem ser subdivididas em: (a) Perda devido a deformao imediata do concreto, (b) Perdas por deslizamento da ancoragem. J as perdas progressivas podem se subdividir em: (c) Perdas por relaxao da armadura, (d) Perdas por retrao no concreto e (e) Perdas por fluncia no concreto.
</p><p>
